import React, { Suspense } from "react";
import styled from "styled-components";
import { OrbitControls } from "@react-three/drei";
import { Canvas } from "@react-three/fiber";
import Cube from "./Cube";
import blockchainback from "../asset/img/Blockchaineth.glb";
import Navbar from "./Navbar";
const Section = styled.div`
  height: 100vh;
  color: white;
  scroll-snap-align: start;
  display: flex;
  flex-direction: column;
`;
const Container = styled.div`
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
`;
const Front = styled.div`
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
`;
const Back = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 0;
`;
const Title = styled.div`
  text-align: center;
  font-size: 10rem;
  font-weight: bold;
  line-height: 0.9em;
`;
const Desc = styled.div`
  text-align: center;
  font-size: 1.5rem;
`;

const Button = styled.button`
  display: block;
  width: 150px;
  font-size: 1.2rem;
  padding: 20px;
  cursor: pointer;
  text-align: center;
  outline: none;
  color: white;
  background-color: rebeccapurple;
  border: none;
  border-radius: 40px;
  box-shadow: 0 4px #999;
  &: hover {
    background-color: #7e3fbd;
  }

  &: active {
    background-color: #7e3fbd;
    box-shadow: 0 2px #666;
    transform: translateY(4px);
  }
`;

const HERO = () => {
  return (
    <Section>
      <Navbar />
      <Container>
        <Front>
          <Title>
            DESIGNING <br /> EVOLUTION
          </Title>
          <br />
          <Desc>
            Eager to leverage Web2 experience while delving deeper into the
            world of Web3
            <br /> creating impactful solutions in <b> DeFi</b>, <b>DAO</b> and{" "}
            <b>Web3</b>-oriented spaces.
          </Desc>
          <Button>Contact Me</Button>
        </Front>
        <Back>
          <Canvas camera={{ position: [0, 0, 20], fov: 75 }}>
            <Suspense fallback={null}>
              <ambientLight intensity={0.8} />
              <directionalLight position={[10, 10, -10]} />
              <Cube glbfile={blockchainback} />
              <OrbitControls enableZoom={false} autoRotate />
            </Suspense>
          </Canvas>
        </Back>
      </Container>
    </Section>
  );
};

export default HERO;
