import React, { Suspense } from "react";
import styled from "styled-components";
import { OrbitControls } from "@react-three/drei";
import { Canvas } from "@react-three/fiber";
import Cube from "./Cube";
import html from "../asset/img/Htmllogo1.glb";
import Reactjs from "../asset/img/1661753280.glb";
import js from "../asset/img/1661753173.glb";
import github from "../asset/img/1661752995.glb";
import metamask from "../asset/img/metamask.glb";
const Section = styled.div`
  height: 100vh;
  scroll-snap-align: center;
  display: flex;
  justify-content: center;
`;

const Container = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
`;

const Left = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  column-gap: 50px;
  @media only screen and (max-width: 768px) {
    display: none;
  }
`;
const ElementContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
`;

const Element = styled.div`
  width: 150px;
`;

const Right = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  @media only screen and (max-width: 768px) {
    align-items: center;
    text-align: center;
  }
`;
const Title = styled.h1`
  color: white;
  font-size: 74px;
  @media only screen and (max-width: 768px) {
    font-size: 60px;
  }
`;
// const WhatWeDo = styled.div`
